

























































	import fIcon from '@/components/m-icon/f-icon.vue'
	import guide from '@/components/guide/guide.vue';
	import {mapState,mapGetters} from 'vuex'
	




	export default {
		components: {fIcon, guide},
		data() {
			return{
				from:'',
				guideShow:false,
				payType:{id:''},
				outTradeNo:'',
				order:{
					total:0
				},
				isBack:false,
				providerList: [{
					name:'微信支付',
					id:'wxpay',
					loading: false
				},{
					name:'支付宝支付',
					id:'alipay',
					loading: false
				}],
				memberAccount:{id:0}
			}
		},
		computed: {
			...mapState(['target']),
			...mapGetters(['agentMember'])
		},
		methods:{
			pickPayType: function(payType) {
				this.payType = payType;
			},
			closeGuide:function(){
				this.guideShow = false;
				let that = this;
				uni.showModal({
					title: '确认',
					content: '已完成支付？',
					success: function (res) {
						if (res.confirm) {
							that.queryPayStatus();
						} else if (res.cancel) {
							
						}
					}
				});
			},
			open:function(){
				uni.redirectTo({
					url:'/pages/user/vip?from=payment'
				})
			},
			doSubmit:function(){
				if(this.payType.id == ''){
					uni.showToast({
						icon:'none',
						title:'请选择支付方式'
					})
					return;
				}
				this.requestPayment(this.payType);
			},
			paySuccess:function(){
				if(this.from == 'user'){
					//个人中心：开通会员支付或充值
					uni.navigateBack();
				}else if(this.from == 'index' || this.from == 'members'){
					//约见：开通会员支付
					uni.redirectTo({
						url:'/pages/form/apply?targetMemberId='+this.target.member.id
					})
				}else if(this.from == 'activity'){
					//活动报名支付
					uni.redirectTo({
						url:'/pages/activity/activity?id='+this.target.activity.id
					})
				}else if(this.from == 'payment'){
					//活动报名支付：开通会员支付或充值
					uni.redirectTo({
						url:'/pages/payment/payment?from=activity&outTradeNo='+this.outTradeNo
					})
				}
			},
			// comeback:function(activity){
			// 	uni.redirectTo({
			// 		url:'/pages/activity/activity?id='+activity.activityId
			// 	})
			// },
			// invite:function(activity){
			// 	uni.redirectTo({
			// 		url:'/pages/member/members?activityId='+activity.activityId
			// 	})
			// },
			initProvider(){
				uni.getProvider({
					service: "payment",
					success: (e) => {
						console.log("payment success:" + JSON.stringify(e));
						let providerList = [];
						e.provider.map((value) => {
							switch (value) {
								case 'alipay':
									providerList.push({
										name: '支付宝',
										id: value,
										loading: false
									});
									break;
								case 'wxpay':
									providerList.push({
										name: '微信',
										id: value,
										loading: false
									});
									break;
								default:
									break;
							}
						})
						this.providerList = providerList;
					},
					fail: (e) => {
						console.log("获取支付通道失败：", e);
					}
				});
			},
			async getPayOrderInfo(payType) {
				var [error, res] = await uni.request({
					url: this.$http.contextPath + 'payment/ali/order',
					method:'POST',
					data:{
						body:'阅见相亲',
						subject:'会员支付',
						productCode:'QUICK_MSECURITY_PAY',
						outTradeNo:this.outTradeNo,
						totalAmount:this.order.total/100
					}
				});
				if(this.$http.success(res)){
					return res.data.msg;
				}
			},
			async alipay(payType){
				let orderInfo = await this.getPayOrderInfo(payType.id);
				console.log(JSON.stringify(orderInfo));
				// if (orderInfo.statusCode !== 200) {
				if (!orderInfo) {
				    uni.showModal({
				        content: "获得订单信息失败",
				        showCancel: false
				    })
				    return;
				}
				uni.requestPayment({
				    provider: payType.id,
				    orderInfo: orderInfo,
				    success: (e) => {
				        // console.log("success", e);
				        uni.showToast({
				            title: "支付成功!"
				        })
						this.paySuccess();
				    },
				    fail: (e) => {
				        console.log("fail", e);
				        uni.showModal({
				            content: "支付失败,原因为: " + e.errMsg,
				            showCancel: false
				        })
				    },
				    complete: () => {
				        payType.loading = false;
				    }
				})
			},
			yjpay:function(){
				let that = this;
				uni.request({
					url: this.$http.contextPath + 'payment/common/pay',
					method:'POST',
					data:{
						outTradeNo:this.outTradeNo,
					},
					success(res) {
						if(that.$http.success(res)){
							if(res.data.data){
								that.memberAccount = res.data.data;
								uni.showToast({
									title:'支付成功'
								})
								that.paySuccess();
							}
						}
					}
				});
			},
			async requestPayment(payType) {
                payType.loading = true;
				if(payType.id == 'alipay'){
					this.alipay(payType);
				}else if(payType.id == 'wxpay'){
					uni.showToast({
						icon:'none',
						title:'即将开通，请尝试其它方式'
					})
				}else if(payType.id == 'yjpay'){
					this.yjpay();
				}
            },
            getOrderInfo(e) {
                let appid = "";




                let url = 'https://demo.dcloud.net.cn/payment/?payid=' + e + '&appid=' + appid + '&total=' + 1/100;
                return new Promise((res) => {
                    uni.request({
                        url: url,
                        success: (result) => {
                            res(result);
                        },
                        fail: (e) => {
                            res(e);
                        }
                    })
                })
            },
			async getOrder(outTradeNo) {
				var [error, res] = await uni.request({
					url: this.$http.contextPath + 'payment/common/getOrder',
					method:'POST',
					data:outTradeNo
				});
				if(this.$http.success(res)){
					this.order = res.data.data;
					return res;
				}
			},
			getMemberAccount:function(){
				//获取会员账户
				let that = this;
				uni.request({
					url: this.$http.contextPath + 'base/member-account/getOne',
					method:'POST',
					data:{
						agentAccountId:this.agentMember.agentAccountId,
						status:'1'
					},
					success(res) {
						if(that.$http.success(res)){
							if(res.data.data){
								that.memberAccount = res.data.data;
							}
						}
					}
				});
			},
		},
		onLoad(option) {
			this.from = option.from;
			if(this.from == 'activity'){
				this.getMemberAccount();
			}
			



			// if(this.providerList.length > 0){
			// 	this.payType = this.providerList[0];
			// }
			
			if(option.out_trade_no){	//支付宝支付返回
				this.isBack = true;
				this.outTradeNo = option.out_trade_no;
				this.queryPayStatus();
			}else{
				this.outTradeNo = option.outTradeNo;
				this.getOrder(this.outTradeNo);
			}
		}
	}
	
	
